import type { TruckLog } from "./types";

/**
 * Simulates saving a truck log to local/XP system storage
 * In production, this would make an API call to save to a database
 */
export async function saveTruckLog(log: Omit<TruckLog, "id" | "createdAt" | "sentToCustoms">): Promise<TruckLog> {
  // Simulate network delay
  await new Promise((resolve) => setTimeout(resolve, 500));

  // In a real app, this would be an API call
  const savedLog: TruckLog = {
    ...log,
    id: `truck-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
    createdAt: new Date().toISOString(),
    sentToCustoms: false,
  };

  return savedLog;
}

/**
 * Simulates sending a saved truck log to Mongolian Customs (Gaaliin gazar)
 * The log must be saved first (have an id)
 */
export async function sendTruckLogToCustoms(logId: string): Promise<{ success: boolean; error?: string }> {
  // Simulate network delay
  await new Promise((resolve) => setTimeout(resolve, 1000));

  // Simulate occasional failures (10% chance)
  if (Math.random() < 0.1) {
    return {
      success: false,
      error: "Network error: Unable to connect to customs system",
    };
  }

  // In a real app, this would be an API call to the customs system
  return {
    success: true,
  };
}

