"use client"

import { useState } from "react"
import { Sidebar } from "@/components/sidebar"
import { Header } from "@/components/header"
import { SummaryCards } from "@/components/summary-cards"
import { TruckInSection } from "@/components/truck-in-section"
import { TruckOutSection } from "@/components/truck-out-section"
import { HistoryTable } from "@/components/history-table"
import { Toaster } from "@/components/ui/toaster"
import type { TruckLog } from "@/lib/types"

export default function DashboardPage() {
  const [logs, setLogs] = useState<TruckLog[]>([])

  const handleSave = (log: TruckLog) => {
    // Update existing log or add new one
    setLogs((prevLogs) => {
      const existingIndex = prevLogs.findIndex((l) => l.id === log.id)
      if (existingIndex >= 0) {
        const updated = [...prevLogs]
        updated[existingIndex] = log
        return updated
      }
      return [log, ...prevLogs]
    })
  }

  const handleUpdateLog = (updatedLog: TruckLog) => {
    setLogs((prevLogs) =>
      prevLogs.map((log) => (log.id === updatedLog.id ? updatedLog : log))
    )
  }

  return (
    <div className="flex h-screen bg-background">
      <Sidebar />
      <div className="flex-1 flex flex-col overflow-hidden">
        <Header />
        <main className="flex-1 overflow-auto p-6 bg-muted/30">
          <div className="max-w-7xl mx-auto space-y-6">
            {/* Summary Cards */}
            <SummaryCards />

            {/* Main Sections: Truck IN and Truck OUT */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <TruckInSection onSave={handleSave} />
              <TruckOutSection onSave={handleSave} />
            </div>

            {/* History Table */}
            <HistoryTable logs={logs} onUpdateLog={handleUpdateLog} />
          </div>
        </main>
      </div>
      <Toaster />
    </div>
  )
}
