"use client"

import { Card, CardContent } from "@/components/ui/card"
import { Truck, AlertCircle, Send, Clock } from "lucide-react"

interface SummaryCard {
  title: string
  value: string | number
  trend?: string
  icon: React.ComponentType<{ className?: string }>
  trendPositive?: boolean
}

const summaryData: SummaryCard[] = [
  {
    title: "Total Trucks Today",
    value: 47,
    trend: "+12 from yesterday",
    icon: Truck,
    trendPositive: true,
  },
  {
    title: "Alerts",
    value: 3,
    trend: "2 require attention",
    icon: AlertCircle,
    trendPositive: false,
  },
  {
    title: "Pending Sends",
    value: 8,
    trend: "Awaiting customs",
    icon: Send,
    trendPositive: false,
  },
  {
    title: "Avg Processing Time",
    value: "2.4 min",
    trend: "-0.3 min improvement",
    icon: Clock,
    trendPositive: true,
  },
]

export function SummaryCards() {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
      {summaryData.map((card) => {
        const Icon = card.icon
        return (
          <Card key={card.title} className="shadow-sm">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div className="flex-1">
                  <p className="text-sm text-muted-foreground mb-1">{card.title}</p>
                  <p className="text-2xl font-bold text-foreground mb-1">{card.value}</p>
                  {card.trend && (
                    <p
                      className={`text-xs ${
                        card.trendPositive
                          ? "text-success"
                          : "text-muted-foreground"
                      }`}
                    >
                      {card.trend}
                    </p>
                  )}
                </div>
                <div className="p-3 rounded-lg bg-primary/10">
                  <Icon className="h-5 w-5 text-primary" />
                </div>
              </div>
            </CardContent>
          </Card>
        )
      })}
    </div>
  )
}

