"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Camera, Save, Send } from "lucide-react"
import { useToast } from "@/hooks/use-toast"
import { saveTruckLog, sendTruckLogToCustoms } from "@/lib/api"
import type { Direction, TruckLog } from "@/lib/types"

interface TruckOutSectionProps {
  onSave: (log: TruckLog) => void
}

export function TruckOutSection({ onSave }: TruckOutSectionProps) {
  const { toast } = useToast()
  const [savedLog, setSavedLog] = useState<TruckLog | null>(null)
  const [isSaving, setIsSaving] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const [errors, setErrors] = useState<Record<string, string>>({})

  // Form state
  const [driverName, setDriverName] = useState("")
  const [cargoType, setCargoType] = useState("")
  const [netWeight, setNetWeight] = useState("")
  const [comments, setComments] = useState("")

  // Mock plate recognition data
  const plateData = {
    plate: "5678",
    confidence: 91.2,
    timestamp: new Date().toLocaleString("en-US", {
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    }),
  }

  const validateForm = (): boolean => {
    const newErrors: Record<string, string> = {}

    if (!plateData.plate) {
      newErrors.plate = "License plate is required"
    }
    if (!driverName.trim()) {
      newErrors.driverName = "Driver name is required"
    }
    if (!cargoType.trim()) {
      newErrors.cargoType = "Cargo type is required"
    }
    if (!netWeight.trim()) {
      newErrors.weight = "Net weight is required"
    } else if (isNaN(Number(netWeight)) || Number(netWeight) <= 0) {
      newErrors.weight = "Weight must be a positive number"
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSave = async () => {
    if (!validateForm()) {
      return
    }

    setIsSaving(true)
    try {
      const log = await saveTruckLog({
        direction: "OUT" as Direction,
        plate: plateData.plate,
        driverName: driverName.trim(),
        cargoType: cargoType.trim(),
        weightKg: Number(netWeight),
        comments: comments.trim() || undefined,
      })

      setSavedLog(log)
      onSave(log)

      toast({
        title: "Success",
        description: "Outbound truck saved to log",
      })

      // Reset form
      setDriverName("")
      setCargoType("")
      setNetWeight("")
      setComments("")
      setErrors({})
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to save truck log",
        variant: "destructive",
      })
    } finally {
      setIsSaving(false)
    }
  }

  const handleSend = async () => {
    if (!savedLog) return

    setIsSending(true)
    try {
      const result = await sendTruckLogToCustoms(savedLog.id)

      if (result.success) {
        const updatedLog = { ...savedLog, sentToCustoms: true }
        setSavedLog(updatedLog)
        onSave(updatedLog)

        toast({
          title: "Success",
          description: "Data successfully sent to Mongolian Customs",
        })
      } else {
        toast({
          title: "Error",
          description: result.error || "Failed to send to customs",
          variant: "destructive",
        })
      }
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to send to customs",
        variant: "destructive",
      })
    } finally {
      setIsSending(false)
    }
  }

  return (
    <Card className="shadow-sm">
      <CardHeader>
        <CardTitle className="text-xl">Truck OUT  Gate Exit</CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* License Plate Recognition */}
        <div className="space-y-4">
          <h3 className="text-sm font-semibold text-foreground">
            Real-Time License Plate Recognition
          </h3>
          <div className="relative w-full h-48 bg-muted rounded-lg overflow-hidden border">
            <div className="absolute inset-0 flex items-center justify-center">
              <Camera className="h-12 w-12 text-muted-foreground" />
            </div>
            <div className="absolute bottom-0 left-0 right-0 bg-black/60 text-white p-3">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-lg font-mono font-bold">{plateData.plate}</p>
                  <p className="text-xs text-gray-300">
                    Confidence: {plateData.confidence}%
                  </p>
                </div>
                <p className="text-xs text-gray-300">{plateData.timestamp}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Form Fields */}
        <div className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="driver-name-out">Driver Name</Label>
            <Input
              id="driver-name-out"
              value={driverName}
              onChange={(e) => setDriverName(e.target.value)}
              placeholder="Enter driver name"
              aria-invalid={!!errors.driverName}
            />
            {errors.driverName && (
              <p className="text-xs text-destructive">{errors.driverName}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="cargo-type-out">Cargo Type</Label>
            <Select value={cargoType} onValueChange={setCargoType}>
              <SelectTrigger id="cargo-type-out" aria-invalid={!!errors.cargoType}>
                <SelectValue placeholder="Select cargo type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="general">General Cargo</SelectItem>
                <SelectItem value="construction">Construction Materials</SelectItem>
                <SelectItem value="food">Food Products</SelectItem>
                <SelectItem value="machinery">Machinery</SelectItem>
                <SelectItem value="other">Other</SelectItem>
              </SelectContent>
            </Select>
            {errors.cargoType && (
              <p className="text-xs text-destructive">{errors.cargoType}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="net-weight-out">Net Weight / Remaining Cargo (kg)</Label>
            <Input
              id="net-weight-out"
              type="number"
              value={netWeight}
              onChange={(e) => setNetWeight(e.target.value)}
              placeholder="Enter net weight"
              aria-invalid={!!errors.weight}
            />
            {errors.weight && (
              <p className="text-xs text-destructive">{errors.weight}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="comments-out">Comments (Optional)</Label>
            <Textarea
              id="comments-out"
              value={comments}
              onChange={(e) => setComments(e.target.value)}
              placeholder="Additional notes..."
              rows={3}
            />
          </div>
        </div>

        {/* Action Buttons */}
        <div className="flex gap-3 pt-4 border-t">
          <Button
            variant="outline"
            onClick={handleSave}
            disabled={isSaving || isSending}
            className="flex-1"
          >
            <Save className="h-4 w-4 mr-2" />
            {isSaving ? "Saving..." : "Save"}
          </Button>
          <Button
            onClick={handleSend}
            disabled={!savedLog || isSending || savedLog.sentToCustoms}
            className="flex-1"
          >
            <Send className="h-4 w-4 mr-2" />
            {isSending ? "Sending..." : "Send to Customs"}
          </Button>
        </div>

        {savedLog && savedLog.sentToCustoms && (
          <p className="text-xs text-success text-center">
             Sent to Mongolian Customs
          </p>
        )}
      </CardContent>
    </Card>
  )
}

