"use client"

import { useState, useMemo } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Search, Send } from "lucide-react"
import { useToast } from "@/hooks/use-toast"
import { sendTruckLogToCustoms } from "@/lib/api"
import type { Direction, TruckLog } from "@/lib/types"

interface HistoryTableProps {
  logs: TruckLog[]
  onUpdateLog: (log: TruckLog) => void
}

export function HistoryTable({ logs, onUpdateLog }: HistoryTableProps) {
  const { toast } = useToast()
  const [directionFilter, setDirectionFilter] = useState<"ALL" | Direction>("ALL")
  const [searchQuery, setSearchQuery] = useState("")
  const [sendingIds, setSendingIds] = useState<Set<string>>(new Set())

  const filteredLogs = useMemo(() => {
    return logs.filter((log) => {
      const matchesDirection =
        directionFilter === "ALL" || log.direction === directionFilter
      const matchesSearch =
        searchQuery === "" ||
        log.plate.toLowerCase().includes(searchQuery.toLowerCase()) ||
        log.driverName.toLowerCase().includes(searchQuery.toLowerCase()) ||
        log.cargoType.toLowerCase().includes(searchQuery.toLowerCase())
      return matchesDirection && matchesSearch
    })
  }, [logs, directionFilter, searchQuery])

  const handleResend = async (log: TruckLog) => {
    if (log.sentToCustoms) return

    setSendingIds((prev) => new Set(prev).add(log.id))
    try {
      const result = await sendTruckLogToCustoms(log.id)

      if (result.success) {
        const updatedLog = { ...log, sentToCustoms: true }
        onUpdateLog(updatedLog)

        toast({
          title: "Success",
          description: "Data successfully sent to Mongolian Customs",
        })
      } else {
        toast({
          title: "Error",
          description: result.error || "Failed to send to customs",
          variant: "destructive",
        })
      }
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to send to customs",
        variant: "destructive",
      })
    } finally {
      setSendingIds((prev) => {
        const next = new Set(prev)
        next.delete(log.id)
        return next
      })
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleString("en-US", {
      month: "short",
      day: "numeric",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    })
  }

  return (
    <Card className="shadow-sm">
      <CardHeader>
        <CardTitle className="text-xl">Truck Log History</CardTitle>
      </CardHeader>
      <CardContent>
        {/* Filters */}
        <div className="flex flex-col sm:flex-row gap-4 mb-6">
          <div className="relative flex-1">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="Search by plate number, driver, or cargo..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-10"
            />
          </div>
          <Select
            value={directionFilter}
            onValueChange={(value) => setDirectionFilter(value as "ALL" | Direction)}
          >
            <SelectTrigger className="w-full sm:w-[180px]">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="ALL">All Directions</SelectItem>
              <SelectItem value="IN">IN Only</SelectItem>
              <SelectItem value="OUT">OUT Only</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Table */}
        <div className="rounded-md border">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Direction</TableHead>
                <TableHead>Plate</TableHead>
                <TableHead>Driver</TableHead>
                <TableHead>Cargo Type</TableHead>
                <TableHead>Weight (kg)</TableHead>
                <TableHead>Created At</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredLogs.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={8} className="text-center py-8 text-muted-foreground">
                    No logs found. Save a truck entry to see it here.
                  </TableCell>
                </TableRow>
              ) : (
                filteredLogs.map((log) => (
                  <TableRow key={log.id}>
                    <TableCell>
                      <Badge
                        variant={log.direction === "IN" ? "default" : "secondary"}
                      >
                        {log.direction}
                      </Badge>
                    </TableCell>
                    <TableCell className="font-mono font-medium">{log.plate}</TableCell>
                    <TableCell>{log.driverName}</TableCell>
                    <TableCell className="capitalize">{log.cargoType}</TableCell>
                    <TableCell>{log.weightKg?.toLocaleString() || ""}</TableCell>
                    <TableCell className="text-sm text-muted-foreground">
                      {formatDate(log.createdAt)}
                    </TableCell>
                    <TableCell>
                      {log.sentToCustoms ? (
                        <Badge variant="default" className="bg-success text-success-foreground">
                          Sent
                        </Badge>
                      ) : (
                        <Badge variant="outline" className="text-muted-foreground">
                          Saved only
                        </Badge>
                      )}
                    </TableCell>
                    <TableCell>
                      {!log.sentToCustoms && (
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleResend(log)}
                          disabled={sendingIds.has(log.id)}
                          className="h-8"
                        >
                          <Send className="h-3 w-3 mr-1" />
                          {sendingIds.has(log.id) ? "Sending..." : "Resend"}
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </div>

        {filteredLogs.length > 0 && (
          <p className="text-sm text-muted-foreground mt-4">
            Showing {filteredLogs.length} of {logs.length} log{logs.length !== 1 ? "s" : ""}
          </p>
        )}
      </CardContent>
    </Card>
  )
}
